import {
    v4 as uuidv4
} from "uuid";

export default class MySocket {
    constructor(draw, type, flow, x, y, state) {
        const self = this;
        var color = "#fff";
        if (type === "exec") {
            color = "#d64c11";
        } else if (type === "int") {
            color = "#32cd32";
        }

        const socket = draw.group();
        const polygon = socket.polygon("0,0 20,0 30,10 20,20 0,20").attr({ "fill": color });
        socket.move(x, y);

        socket.on("mouseenter", function () {
            console.log("I am in!");
            state.canDrag = false;
            polygon.fill("#0000ff");
            socket.css("cursor", "pointer");
            if (state.makingConnection) {
                if (state.endSocket !== self) {
                    state.endSocket = self;
                    console.log("endSocket");
                }
            }
        });
        socket.on("mouseleave", function () {
            console.log("I am out!");
            state.canDrag = true;
            polygon.fill(color);
            socket.css("cursor", null);
            if (state.makingConnection) {
                state.endSocket = null;
            }
        });
        socket.on("mousedown", function () {
            if (flow === "out") {
                console.log("start to draw");
                state.makingConnection = true;
                state.startSocket = self;
            }
        });
        socket.on("click", function () {
            alert(self.id);
        });

        self.flow = flow;
        self.svg = socket;
        self.type = type;
        self.id = uuidv4();
    }
}