<template>
<div id="drawing" class="canvas">
</div>
</template>

<script>
import {
    onMounted
} from "vue";
import {
    SVG
} from "@svgdotjs/svg.js";
import "@svgdotjs/svg.draggable.js";
import {
    v4 as uuidv4
} from "uuid";
import MyNode from "@/models/MyNode.js";

export default {
    setup() {
        const allPath = new Map();
        const pathSockets = new Map(); // start/out -> end/in
        const state = {
            makingConnection: false,
            startSocket: null,
            endSocket: null,
            canDrag: true
        };
        console.log(allPath);
        console.log(state);

        onMounted(function () {
            const draw = SVG().addTo("#drawing").size("100vw", "100vh");
            const drawingPath = draw.path("M 0 0 L 10 10");
            drawingPath.stroke({
                color: "#d61c00",
                width: 5,
                linecap: "round",
                linejoin: "round"
            });

            draw.on("mouseup", function () {
                console.log("up");
                if (state.makingConnection) {
                    if (!state.endSocket) {
                        drawingPath.hide();
                    } else {
                        drawingPath.hide();
                        if (pathSockets.get(state.startSocket.id) === state.endSocket.id) {
                            alert("Cannot make duplicate connection!");
                        } else {
                            console.log("make connection!");
                            const pathId = uuidv4();
                            const path = draw.path("M " + state.startSocket.svg.cx() + " " + state.startSocket.svg.cy() + " L " + state.endSocket.svg.cx() + " " + state.endSocket.svg.cy());
                            path.stroke({
                                color: "#a63c01",
                                width: 5,
                                linecap: "round",
                                linejoin: "round"
                            });
                            allPath.set(pathId, path);

                            node1.addConnection(pathId, state.startSocket, state.endSocket);
                            node2.addConnection(pathId, state.startSocket, state.endSocket);
                            pathSockets.set(state.startSocket.id, state.endSocket.id);
                        }
                    }
                }
                //Clear everything
                state.makingConnection = false;
                state.startSocket = null;
                state.endSocket = null;
            });
            draw.on("mousemove", function (e) {
                if (state.makingConnection) {
                    const startX = state.startSocket.svg.cx();
                    const startY = state.startSocket.svg.cy();
                    const endX = e.offsetX;
                    const endY = e.offsetY;
                    drawingPath.plot("M " + startX + " " + startY + " L " + endX + " " + endY);
                    drawingPath.show();
                }
            });

            const node1 = new MyNode(draw, state, allPath, "Function 1");
            node1.addSocket("exec", "out");
            node1.addSocket("int", "out");

            const node2 = new MyNode(draw, state, allPath, "Function 2");
            node2.addSocket("exec", "in");
            node2.addSocket("exec", "out");
            node2.addSocket("int", "out");
        });
    }
}
</script>

<style scoped>
.canvas {
    user-select: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
}
</style>
